import {LitElement, html, css, customElement, property} from 'lit-element';
import {isMobile} from '../../../utils/response-query';

@customElement('uc-header-search')
class UcHeaderSearch extends LitElement {
    @property({type: String}) action;

    static styles = css`
        :host {
            display: block; }
        @media (max-width: 991px) and (min-width: 768px) {
        :host {
                width: 22px;
                height: 22px; } }
        @media (max-width: 767px) {
        :host {
                width: 1.57143em;
                height: 1.57143em; } }
        
        ::slotted(form) {
            display: none; }
        
        .icon {
            display: block;
            width: 100%;
            height: 100%;
            background-image: url(data:image/svg+xml;base64,);
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat; }
        
        form {
            display: flex;
            height: 100%;
            width: 100%; }
        
        input {
            flex: 1 1 90%;
            padding: 12px 18px 12px;
            border: 3px solid #66c05d;
            font-size: 12px;
            color: #999999; }
        
        button {
            width: 40px;
            height: 40px;
            background-color: #66c05d;
            border: none;
            background-size: 14px;
            background-position: center;
            background-repeat: no-repeat;
            background-image: url(data:image/svg+xml;base64,); }

    `;

    render() {
        return isMobile() ? html`
            <div class="icon"></div>
        ` : html`
            <form method="get" action="${this.action}">
                <input type="text" name="query" placeholder="Поиск по советам, товарам, услугам"/>
                <button type="submit"></button>
            </form>
        `;
    }
}



